$black: #000000;
$grey-light : #2e2e2e;
$blue-bg : #4d2efd;

@media (min-width: 992px) {

    .main {
        // border: 5px solid #000000;
        box-shadow: 0 0 5px #000000;
        border-radius: 20px;
        height: 90vh;
        margin: 5vh auto;
        min-height: 500px;
        min-width: 900px;
        overflow: hidden;
        position: relative;
        width: 90%;
    }
    .header {
        width: 100%;
        min-height: 10%;
        background-color: $grey-light;
        position: relative;
        .nav {
            top: 50%;
            left: 10%;
            position: absolute;
            width: 80%;
            background-color: $blue-bg;
            border-radius: 20px;
            height: 50px;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            box-shadow: 0 0 5px #000000;
            .nav-item {
                cursor: pointer;
                min-width: 70px;
                height: 100%;
                text-align: center;
                text-decoration: none;
                line-height: 50px;
                font-size: 1.2rem;
                color: #fafafa;
                padding: 0 10px;
                &:hover {
                    background-color: #00000038;
                    box-shadow: 0 0 3px #000000;
                }
            }
        }
    }
    .ppal{
        background-image: url('/assets/images/bg.png');
        height: 80%;
        overflow-y: scroll;
        width: 100%;
        padding-top: 30px;
        .container{
            height: 100%;
            width: 100%;
            display: grid;
            grid-template-columns: 40% 60%;

            .img-ppal{
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                padding: 10px;
                img{
                    display: block;
                    width: 100%;
                }
            }
            .body-ppal{
                width: 90%;
                margin: 0 auto;
                background-color: #00000000;
                height: 90%;
                border-radius: 10px;
                padding: 20px;
                transition: 500ms;
                box-shadow: 0px 0px 3px rgb(145, 145, 145);
                &:hover{
                    box-shadow: 0px 0px 15px rgb(145, 145, 145);
                }
                .title{
                    text-align: center;
                    color: #999999;
                }
            }
        }
    }
    .footer{
        color: #fff;
        width: 100%;
        background-color: $grey-light;
        height: 10%;
        top: 90%;
        position: absolute;
        text-align: center;
        line-height: 50px;
    }

    .fondo-right {
        position: absolute;
        border-right: 90vw solid transparent;
        border-bottom: 90vh solid $grey-light;
        z-index: -1000;
    }
    .fondo-left {
        position: absolute;
        border-left: 90vw solid transparent;
        border-bottom: 90vh solid #f7f7f7;
        position: absolute;
        z-index: -1000;
    }
}
