$blue-transparent: #0097b7;
$title-color: #d9ffe8;
$icon-color: #aa8f8f;
$icon-top-color: #e8bfbf;
@media (max-width: 600px) {
    .player {
        // background-image: url("/assets/images/textura.png");
        background-color: black;
        height: 100vh;
        width: 100%;
        .player-container {
            width: 100%;
            height: 100%;
            .player-header {
                color: $icon-top-color;
                background-color: $blue-transparent;
                display: grid;
                grid-template-columns: 10% 80% 10%;
                align-items: center;
                height: 12%;
                opacity: 76%;
                padding: 5px;
                width: 100%;

                i {
                    font-size: 2.2rem;
                    text-align: center;
                }
                .song-title {
                    display: block;
                    width: 90%;
                    // height: 50%;
                    line-height: 50px;
                    font-size: 1.6rem;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
            .player-body {
                width: 100%;
                height: 68%;
                // background-color: $title-color;
                display: flex;
                justify-content: center;
                align-items: center;
                .circle {
                    width: 250px;
                    height: 250px;
                    background: radial-gradient(circle, #ffffff 0%, $blue-transparent 100%);
                    border: 10px solid $blue-transparent;
                    border-radius: 150px;
                    opacity: 68%;
                    display: flex;
                    align-items: center;
                    overflow: hidden;
                    
                    .figura{
                        border-bottom: 50px solid transparent;
                        border-top: 50px solid transparent;
                        border-right: 125px solid $blue-transparent;
                        border-left: 125px solid $blue-transparent;
                    }
                }

            }
            .player-footer {
                width: 100%;
                height: 20%;
                background-color: $blue-transparent;
                opacity: 76%;
                position: relative;
                display: grid;
                grid-template-columns: 10% 90%;
                align-items: center;
                padding: 10px;

                .slide {
                    display: block;
                    width: 100%;
                    border: none;
                    background-color: $icon-color;
                    position: absolute;
                    height: 15px;
                    top: -14px;
                    -webkit-appearance: none;
                    &::-webkit-slider-runnable-track {
                        background-color: $icon-color;
                        width: 100%;
                        height: 100%;
                    }
                    &::-webkit-slider-thumb {
                        top: -8px;
                        position: relative;
                        -webkit-appearance: none;
                        width: 30px;
                        height: 30px;
                        border-radius: 15px;
                        background-color: #8daacb;
                    }
                }
                .fa-redo-alt{
                    font-size: 3rem;
                    color: $icon-color;
                    text-align: center;
                }
                .media-group{
                    width: 80%;
                    justify-self: end;
                    height: 100%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    color: $icon-color;
                    .fa-fast-backward, .fa-fast-forward{
                        font-size: 2.5rem;
                    }
                    .fa-play-circle, .fa-pause-circle{
                        font-size: 4rem;
                        margin: 0 10px;
                    }
                }

            }
            .song-list{
                position: absolute;
                width: 90%;
                height: 100%;
                background-color: #2f444e;
                box-shadow: 2px 0 3px #bebebe;
                z-index: 10;
                display: flex;
                flex-direction: column;
                padding: 10px 0;
                border-left: 1px solid #2f444e;
                transition: 2s all ease;
                left: 0;
                .song-item{
                    font-size: 1.5rem;
                    color: $icon-top-color;
                    width: 100%;
                    overflow-y: scroll;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    padding: 20px 5px;
                    background-color: #0000004d;
                    margin-bottom: 2px;
                    border-left: 3px solid #a0a0a0;
                    &:focus{
                        background-color: #000000;
                    }
                    
                }
                
            }
            .move-menu-left{
                left: -90%;
            }
        }


        //Animations
        .spin-disc{
            animation: spin 4s linear infinite;
        }
        .boderC{
            animation: borderCC 4s linear infinite; 
        }
        @keyframes spin {
            0%{
                border-right: 125px solid rgb(130, 130, 255);
                border-left: 125px solid rgb(130, 130, 255);
                transform: rotate(0deg);
            }
            25%{
                border-right: 125px solid rgb(118, 228, 255);
                border-left: 125px solid rgb(118, 228, 255);
            }
            50%{
                border-right: 125px solid rgb(118, 255, 113);
                border-left: 125px solid rgb(118, 255, 113);
            }
            75%{
                border-right: 125px solid rgb(252, 118, 234);
                border-left: 125px solid rgb(252, 118, 234);
            }
            100%{
                border-right: 125px solid rgb(219, 255, 120);
                border-left: 125px solid rgb(219, 255, 120);
                transform: rotate(360deg);
            }
        }
        @keyframes borderCC {
            0%{
                border: 10px solid rgb(130, 130, 255);
        
            }
            25%{
                border: 5px solid rgb(118, 228, 255);
            }
            50%{
                border: 5px solid rgb(118, 255, 113);
            }
            75%{
                border: 5px solid rgb(252, 118, 234);
            }
            100%{
                border: 5px solid rgb(219, 255, 120);
                
            }
        }
    }
}
